import React from "react";
import Sidebar from "../Sidebar";
import Box from "@mui/material/Box";
import { Typography } from "@mui/material";
import Toolbar from "@mui/material/Toolbar";
import AppBar from "@mui/material/AppBar";
import Searchtab from "../components/Searchtab";
import OrganizationT from "../components/OrganizationT";

const drawerWidth = 240;

export default function Organization() {
  return (
    <div>
      <AppBar
        position="static" // Change position from "fixed" to "static" or any other valid value
        sx={{
          width: `calc(100% - ${drawerWidth}px)`,
          ml: `${drawerWidth}px`,
          background: "#063970",
          // top side
        }}
      >
        <Toolbar>
          <Typography variant="h5" noWrap component={"div"}>
            Organizations
          </Typography>
        </Toolbar>
      </AppBar>

      <Box
        sx={{ display: "grid", gridTemplateColumns: `${drawerWidth}px auto` }}
      >
        <Sidebar />

        <Box component="main" sx={{ bgcolor: "background.default", p: 3 }}>
          <Box
            sx={{
              position: "static",
              paddingBottom: 0,
              paddingTop: 0,
              marginTop: 0,
            }}
          >
            {" "}
            <Searchtab />
          </Box>
          <Toolbar />

          {/* <Box sx={{ marginLeft: 10 }}></Box> */}

          <Typography paragraph>
            <OrganizationT />
            {/* Consequat mauris nunc congue nisi vitae suscipit. Fringilla est
            ullamcorper eget nulla facilisi etiam dignissim diam. Pulvinar
            elementum integer enim neque volutpat ac tincidunt. Ornare
            suspendisse sed nisi lacus sed viverra tellus. Purus sit amet
            volutpat consequat mauris. Elementum eu facilisis sed odio morbi.
            Euismod lacinia at quis risus sed vulputate odio. Morbi tincidunt
            ornare massa eget egestas purus viverra accumsan in. In hendrerit
            gravida rutrum quisque non tellus orci ac. Pellentesque nec nam
            aliquam sem et tortor. Habitant morbi tristique senectus et.
            Adipiscing elit duis tristique sollicitudin nibh sit. Ornare aenean
            euismod elementum nisi quis eleifend. Commodo viverra maecenas
            accumsan lacus vel facilisis. Nulla posuere sollicitudin aliquam
            ultrices sagittis orci a. */}
          </Typography>
        </Box>
      </Box>
    </div>
  );
}
