import * as React from "react";
import { DataGrid } from "@mui/x-data-grid";
import { MuiButton } from "./MuiButton";

const columns = [
  { field: "id", headerName: "ID", width: 70 },
  { field: "Name", headerName: "Name", width: 130 },

  { field: "organization", headerName: "Organization name", width: 230 },
  { field: "userRole", headerName: "Role", width: 130 },

  {
    field: "status",
    headerName: "Status",
    width: 200,
    renderCell: () => <MuiButton />,
  },
];

const rows = [
  { id: 1, Name: "Snow", organization: "ABC Company", userRole: "Admin" },
  { id: 2, Name: "Lannister", organization: "Sysco Labs", userRole: "Admin" },
  { id: 3, Name: "Lannister", organization: "Pick me", userRole: "Developer" },
  { id: 4, Name: "Stark", organization: "Keels", userRole: "Developer" },
  { id: 5, Name: "Targaryen", organization: "OneGalle", userRole: "Developer" },
  {
    id: 6,
    Name: "Melisandre",
    organization: "Lucky Company",
    userRole: "user",
  },
  { id: 7, Name: "Clifford", organization: "Medicare", userRole: "Developer" },
  { id: 8, Name: "Frances", organization: "Lion Company", userRole: "User" },
  { id: 9, Name: "Roxie", organization: "Brandix Company", userRole: "User" },
];

export default function DataTable() {
  return (
    <div style={{ height: 400, width: "100%" }}>
      <DataGrid
        rows={rows}
        columns={columns}
        pageSize={5}
        rowsPerPageOptions={[5, 10]}
        checkboxSelection
      />
    </div>
  );
}
