import * as React from "react";
import { DataGrid } from "@mui/x-data-grid";
import { MuiButton } from "./MuiButton";

const columns = [
  { field: "id", headerName: "ID", width: 200 },
  { field: "organization", headerName: "Organization name", width: 230 },

  {
    field: "status",
    headerName: "Status",
    width: 200,
    renderCell: () => <MuiButton />,
  },
];

const rows = [
  { id: 1, organization: "ABC Company" },
  { id: 2, organization: "Sysco Labs" },
  { id: 3, organization: "Pick me" },
  { id: 4, organization: "Keels" },
  { id: 5, organization: "OneGalle" },
  { id: 6, organization: "Lucky Company" },
  { id: 7, organization: "Medicare" },
  { id: 8, organization: "Lion Company" },
  { id: 9, organization: "Brandix Company" },
];

export default function DataTable() {
  return (
    <div style={{ height: 400, width: "100%" }}>
      <DataGrid
        rows={rows}
        columns={columns}
        pageSize={5}
        rowsPerPageOptions={[5, 10]}
        checkboxSelection
      />
    </div>
  );
}
