import { Stack, Button } from "@mui/material";
//import Alert from "@mui/material/Alert";

export const MuiButton = () => {
  return (
    <div>
      <Stack spacing={2} direction={"row"}>
        <Button
          variant="contained"
          color={"primary"}
          size="small"
          disableElevation
          onClick={() => alert("Successfuly Enable the organization")}
        >
          Enable
        </Button>
        <Button
          variant="outlined"
          color="error"
          size="small"
          disableElevation
          onClick={() => alert("Successfuly Disable the organization")}
        >
          Disable
        </Button>

        {/* <Button variant="outlined" color="error">
          Error
        </Button> */}
      </Stack>
    </div>
  );
};
