import * as React from "react";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import UpdateCancel from "./UpdateCancel";
import AccountCircleOutlinedIcon from "@mui/icons-material/AccountCircleOutlined";
// import p from "./p";

export default function FormPropsTextFields() {
  return (
    <Box
      component="form"
      sx={{
        "& .MuiTextField-root": {
          m: 1,
          width: "25ch",
          marginTop: 0,
          paddingTop: 1,
        },
      }}
      noValidate
      autoComplete="off"
    >
      <AccountCircleOutlinedIcon sx={{}} />
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          "& > :not(style)": { m: 1 },
        }}
      >
        <TextField
          required
          helperText="Please enter your name"
          id="demo-helper-text-aligned"
          label="FirstName"
        />
        <TextField
          helperText=" "
          id="demo-helper-text-aligned-no-helper"
          label="LastName"
        />
      </Box>
      <TextField
        helperText=" "
        id="demo-helper-text-aligned-no-helper"
        label="Organization name"
      />
      <TextField
        helperText=" "
        id="demo-helper-text-aligned-no-helper"
        label="GitHub Profile link"
      />
      <div>
        <TextField
          helperText=" "
          id="demo-helper-text-aligned-no-helper"
          label="Address"
        />
        <TextField
          helperText=" "
          id="demo-helper-text-aligned-no-helper"
          label="Date of Birth"
        />
      </div>
      <div>
        <TextField
          helperText=" "
          id="demo-helper-text-aligned-no-helper"
          label="Skils"
        />
        <TextField
          helperText=" "
          id="demo-helper-text-aligned-no-helper"
          label="Email"
        />
      </div>
      <div>
        <TextField
          id="outlined-password-input"
          label="Password"
          type="password"
          autoComplete="current-password"
        />

        <TextField
          id="outlined-number"
          label="Contact number"
          type="number"
          InputLabelProps={{
            shrink: true,
          }}
        />
      </div>
      {/* <p open={true} /> */}
      <UpdateCancel />
    </Box>
  );
}
